








import Vue from "vue";
import {mapGetters} from "vuex";

export default Vue.extend({
	props: {
		number: {
			type: Number,
			default: 0,
			required: true,
			validator: function(value: number): boolean {
				return value.toString().length <= 3 ? true : false;
			}
		}
	},
	methods: {
		//...mapGetters(["getImg"])
		getImg(order: 1 | 2 | 3) {
			return this.$store.getters.getImg(order, this.number);
		}
	}
});
